/**
 * YAMLJavaScript
 * @param text - 
 * @returns 
 */
export function parseSimpleYaml(text: string): any {
  if (!text) return null;

  const lines = text.split('\n');
  const result: any = {};
  const stack: { obj: any; indent: number }[] = [{ obj: result, indent: -1 }];

  for (const line of lines) {
    if (line.trim() === '') continue;

    const indent = (line.match(/^\s*/) || [''])[0].length;
    const trimmedLine = line.trim();

    while (indent <= stack[stack.length - 1].indent) {
      stack.pop();
    }

    const parent = stack[stack.length - 1].obj;

    if (trimmedLine.startsWith('- ')) {
      const value = trimmedLine.substring(2).trim();
      // 
      if (Array.isArray(parent)) {
        //  "key: value"
        const separatorIndex = value.indexOf(':');
        if (separatorIndex > -1) {
          const key = value.substring(0, separatorIndex).trim();
          const val = value.substring(separatorIndex + 1).trim();
          const newObj: any = {};
          newObj[key] = val;
          parent.push(newObj);
          stack.push({ obj: newObj, indent });
        } else {
          parent.push(value);
        }
      }
    } else {
      const separatorIndex = trimmedLine.indexOf(':');
      if (separatorIndex > -1) {
        const key = trimmedLine.substring(0, separatorIndex).trim();
        const value = trimmedLine.substring(separatorIndex + 1).trim();

        if (value) {
          parent[key] = value;
        } else {
          // /
          const nextLineIndex = lines.indexOf(line) + 1;
          if (nextLineIndex < lines.length && lines[nextLineIndex].trim().startsWith('-')) {
            const newList: any[] = [];
            parent[key] = newList;
            stack.push({ obj: newList, indent });
          } else {
            const newObj = {};
            parent[key] = newObj;
            stack.push({ obj: newObj, indent });
          }
        }
      }
    }
  }

  // ''
  if (Object.keys(result).length === 1 && result['']) {
    return result[''];
  }

  return result;
}
