import { isSimulationRunning } from '../store/getters';
import { dockManagerService } from './DockManagerService';

// Import all panel components once
import AvatarCharacterPanel from '../components/panels/avatar/AvatarCharacterPanel.vue';
import AvatarInventoryPanel from '../components/panels/avatar/AvatarInventoryPanel.vue';
import AvatarTasksPanel from '../components/panels/avatar/AvatarTasksPanel.vue';
import CharacterPanel from '../components/panels/CharacterPanel.vue';
import ImprintsPanel from '../components/panels/ImprintsPanel.vue';
import InventoryPanel from '../components/panels/InventoryPanel.vue';
import SkillsPanel from '../components/panels/SkillsPanel.vue';
import NpcDirectoryPanel from '../components/panels/NpcDirectoryPanel.vue';
import ReincarnationPanel from '../components/panels/ReincarnationPanel.vue';
import RippleDetailPanel from '../components/panels/RippleDetailPanel.vue';
import SystemPanel from '../components/panels/SystemPanel.vue';
import WorldDetailsPanel from '../components/panels/WorldDetailsPanel.vue';
import AvatarWorldMapView from '../modules/world-map/components/AvatarWorldMapView.vue';
import WorldMapView from '../modules/world-map/components/WorldMapView.vue';

import type { Component } from 'vue';

interface PanelInfo {
  component: Component;
  title: string;
}

type PanelRegistry = Record<string, Record<string, PanelInfo>>;

const panelRegistry: PanelRegistry = {
  mainWorld: {
    character: { component: CharacterPanel, title: '' },
    inventory: { component: InventoryPanel, title: '' },
    skills: { component: SkillsPanel, title: '' },
    imprints: { component: ImprintsPanel, title: '' },
    worldDetails: { component: WorldDetailsPanel, title: '' },
    sandbox: { component: WorldMapView, title: '' },
  },
  avatarWorld: {
    character: { component: AvatarCharacterPanel, title: '' },
    inventory: { component: AvatarInventoryPanel, title: '' },
    tasks: { component: AvatarTasksPanel, title: '' },
    worldDetails: { component: WorldDetailsPanel, title: '' },
    sandbox: { component: AvatarWorldMapView, title: '' },
  },
  reincarnation: {
    main: { component: ReincarnationPanel, title: '' },
  },
  allWorlds: {
    npcDirectory: { component: NpcDirectoryPanel, title: '' },
  },
  ripples: {
    detail: { component: RippleDetailPanel, title: '' },
  },
  system: {
    saveLoad: { component: SystemPanel, title: '' },
  },
};

export function usePanelManager() {
  const openPanel = (world: keyof typeof panelRegistry, panel: string, props: Record<string, any> = {}) => {
    if (world === 'avatarWorld' && !isSimulationRunning.value) {
      return;
    }

    const panelInfo = panelRegistry[world]?.[panel];
    if (panelInfo) {
      const panelId = `${world}-${panel}-${props.rippleId || ''}`;
      const finalProps = { worldType: world, ...props };
      dockManagerService.openPanel(panelId, panelInfo.title, panelInfo.component, finalProps);
    }
  };

  return { openPanel };
}
